import "core-js/modules/es6.regexp.replace";
// import layoutMain from '@/layout/main'
import layoutHeader from '@/layout/header';
import home from '@/views/home';
var meta = {
  requiresAuth: true
  /**
   * 在主框架之内显示
   */

};
var frameIn = [{
  path: '/',
  redirect: '/home',
  meta: meta,
  component: layoutHeader,
  children: [{
    path: '/home',
    name: 'home',
    meta: meta,
    component: home
  }, {
    path: '/video',
    name: 'video',
    meta: meta,
    component: function component() {
      return import('@/views/vip-video');
    } // children: (pre => [])('video-')

  }, {
    path: '/author',
    name: 'author',
    meta: meta,
    component: function component() {
      return import('@/views/author');
    }
  }, {
    path: '/setting',
    name: 'setting',
    meta: meta,
    component: function component() {
      return import('@/views/setting');
    }
  }]
}];
/**
 * 在主框架之外显示
 */

var frameOut = [// 页面重定向使用 必须保留
{
  path: '/redirect/:path*',
  component: {
    beforeCreate: function beforeCreate() {
      this.$router.replace(JSON.parse(this.$route.params.path));
    },
    render: function render(h) {
      return h();
    }
  }
}, // 页面刷新 必须保留
{
  path: '/refresh',
  name: 'refresh',
  component: {
    beforeRouteEnter: function beforeRouteEnter(to, from, next) {
      next(function (vm) {
        vm.$router.replace(from.fullPath);
      });
    },
    render: function render(h) {
      return h();
    }
  }
}, // index
{
  path: '/index',
  name: 'index',
  component: function component() {
    return import('@/views/index');
  }
}, //about
{
  path: '/about',
  name: 'about',
  component: function component() {
    return import('@/views/about');
  }
}]; // 重新组织后导出

export default [].concat(frameIn, frameOut);